@import './../../styles/variables.scss';

.input-location {
    font-size: $size-2;
    font-weight: 300;
    border-radius: 2px;
    margin: 0;
    border: none;
    width: 100%;
    background: $transparent;
    transition: padding-top 0.2s ease, margin-top 0.2s ease;
    overflow-x: hidden;
    position: relative;
    padding-top: 60px;
    z-index: 2;
    &:focus + label{
        width: 100%;
    }
}

.input-location + label{
  display: block;
  position: relative;
  white-space: nowrap;
  padding: 0;
  margin: 0;
  width: 100%;
  border-top: 1px solid $purple;
  -webkit-transition: width 0.4s ease;
  transition: width 0.4s ease;
  height: 0px;
}

.input-location:focus + label > span,
.input-location:valid + label > span {
  top: -100px;
  font-size: $size-6;
  color: $white;
}

.input-location + label > span {
  font-weight:200;
  margin: 0;
  position: absolute;
  color: $white;
  font-size: $size-5;
  top: -66px;
  left: 0px;
  z-index: 0;
  -webkit-transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
  transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
}

@media only screen and (min-width: 890px) {
    .input-location {
        font-size: $size-2;
        font-weight: 300;
        border-radius: 2px;
        margin: 0;
        border: none;
        width: 100%;
        background: $transparent;
        transition: padding-top 0.2s ease, margin-top 0.2s ease;
        overflow-x: hidden;
        padding-top: 60px;
        &:focus + label{
            width: 100%;
        }
    }

    .input-location + label{
        display: block;
        position: relative;
        white-space: nowrap;
        padding: 0;
        margin: 0;
        width: 100%;
        border-top: 1px solid $purple;
        -webkit-transition: width 0.4s ease;
        transition: width 0.4s ease;
        height: 0px;
    }

    .input-location:focus + label > span,
    .input-location:valid + label > span{
        top: -100px;
        font-size: $size-4;
        color: $white;
    }

    .input-location + label > span {
        font-weight:200;
        margin: 0;
        position: absolute;
        color: $white;
        font-size: $size-4;
        top: -66px;
        left: 0px;
        z-index: 0;
        -webkit-transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
        transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
    }
}